<template>
	<div class="box">
		<div class="product_box">
			<!---->
			<ul class="nav_header">
				<li></li>
				<li class="right_icon">></li>
				<li>{{dispName}}</li>
				<li class="right_icon">></li>
				<li>{{obj.pname}}</li>
			</ul>
			<!---->
			<div class="product_describe">
				<div class="product_img">
					<div class="big_img">

						<pic-zoom :url='imgUrlList[indexImg] ' :scale="3"></pic-zoom>

						<pic-zoom :url='imgUrlList[indexImg]' :scale="3"></pic-zoom>

					</div>
					<div class="small_img">
						<span class="before"><img src="../../assets/detail/before.png"/></span>
						<div class="box">
							<ul>
								<li v-for="(item,index) in imgUrlList" :key="index" @mouseover="showImg(index)" :class="{border:index==isBorder}"><img :src='item'></li>
							</ul>
						</div>
						<span class="after"><img src="../../assets/detail/after.png"/></span>
					</div>
				</div>
				<div class="product_info">
					<!---->
					<p class="product_name">{{obj.pname}}</p>
					<!-- -->
					<ul class="brand">
						<li> &nbsp;{{brandName}}</li>
						<li class="code">&nbsp;{{productId}}</li>
					</ul>
					<!---->
					<div class="price"><span class="symbol"></span>{{obj.price}}<span class="discount">{{originPrice}}</span></div>
					<!---->
					<ul class="product_detail">
						<li>
							<p class="title send">:</p>
							<p class="region"><span>{{region}}</span><span>7-14</span></p>
						</li>
						<li>
							<p class="title">&nbsp;&nbsp;&nbsp;:</p>
							<p class="shop"></p>
						</li>
						<li>
							<p class="title">:</p>
							<p><span :class="{parent:true,choose:true}">{{obj.firstClassAttrName}}</span></p>
						</li>
						<li>
							<p class="title">:</p>
							<p><span :class="{parent:index==isBlack,choose:true}" v-for="(item,index) in skuAndPriceList" @click="black(index)" ref="menuItem">{{item.subClassAttrName}}</span></p>
						</li>
						<li>
							<p class="title">:</p>
							<div :class="{black:isEnter,input:true}">
								<input type="text" v-model="count" @focus="enterFocus()" @blur="enterBlur()" />
								<div @click="reduce" @mouseover="mouseEnter()" @mouseleave="mouseLeave()" :class="{reduce:true,disable:isDisable}">-</div>
								<div class="add" @click="add" @mouseenter="mouseEnter()" @mouseleave="mouseLeave()">+</div>
							</div>
						</li>
					</ul>
					<!-- -->
					<ul class="service">
						<li><img src="../../assets/detail/shop.png" class="store" /></li>
						<li><i class="fa fa-user-o"></i></li>
					</ul>
					<!---->
					<div class="shopcar">
						<button></button>
						<button @click="shopcar"></button>
					</div>
					<!---->
					<p class="phone"><span>: 400-888-4499</span>(9:00~19:00)</p>
					<!-- -->
					<ul class="shou">
						<li><img src="../../assets/detail/1.png" /></li>
						<li><img src="../../assets/detail/2.png" /></li>
						<li><img src="../../assets/detail/3.png" /></li>
						<li><img src="../../assets/detail/4.png" /></li>
						<li><img src="../../assets/detail/4.png" /></li>
					</ul>
				</div>
			</div>
			<ul class="nav_detail">
				<li :class="{tab:index==!isShow}" @click="show(index)">
				</li>
				<li :class="{tab:index==isShow}" @click="show(index)">
				</li>
			</ul>
			<!---->
			<div class="showdetail" v-show="isActive">
				<!---->
				<div class="title_product"></div>
				<ul class="one">
					<li>():</li>
					<li>{{obj.firstClassAttrName}}</li>
					<li>: </li>
					<li>: </li>
				</ul>
				<ul class="one">
					<li>: </li>
					<li>: </li>
					<li>: </li>
					<li>: </li>
				</ul>
				<ul class="one">
					<li>: </li>
					<li>: </li>
				</ul>
				<productdetail></productdetail>
				<div v-html="productDetail" class="detailImg"></div>
				<Customer title=':'>
					<div class="content" slot='customer'>
						<p>1. </p>
						<p>2. </p>
					</div>
				</Customer>
				<Customer title=':'>
					<div class="content" slot='customer'>
						<p></p>
						<p></p>
						<p></p>
					</div>
				</Customer>
			</div>
			<!---->
			<div class="judgedetail" v-show="!isActive"></div>
		</div>
	</div>
</template>
productParameter
<script>
	import PicZoom from 'vue-piczoom'
	import productdetail from '../../components/productDetail'
	import Customer from '../../components/customer'
	export default {
		components: {
			PicZoom,
			Customer,
			productdetail
		},
		beforeCreate(){
			window.scroll(0,0)
		},
		data() {
			return {

				/**/
				productId:'',
				originPrice:'',
				/**/
				isDisable: false, //1 
				isEnter: false, //
				isBorder: 0,
				indexImg: 0, //
				index: 0,
				isShow: true,
				isActive: true,
				arr: [],
				obj: {
					innerSize: '', //
					count: 1, //
					pname: '',
					price: '',
					firstClassAttrName: '', //
					proPictDir: '',
					checked:true
				},
				innerSize: '',
				isBlack: 0, //
				count: 1, //
				dispName: '',
				proPictDir: '',
				region: '',
				brandName: '',
				firstClassAttrName: '', //
				skuAndPriceList: '', //
				imgUrlList: '', //
				productDetail: '' //
			}
		},
		mounted() {
			this.getData()
		},
		computed:{
			
		},
		methods: {
			getData() {
				this.obj.pname = this.$route.query.pname; //
				this.obj.price = this.$route.query.price; //
				this.originPrice = this.$route.query.originPrice; //
				this.productId = this.$route.query.productId; //
				this.region = this.$route.query.region; //
				this.brandName = this.$route.query.brandName; //
				this.obj.proPictDir = this.$route.query.proPictDir; //
				this.dispName = this.$route.query.dispName; //
				this.obj.firstClassAttrName = this.$route.query.firstClassAttrName; //
				this.skuAndPriceList = this.$route.query.skuAndPriceList; //
				this.obj.innerSize = this.skuAndPriceList[0].subClassAttrName
				this.imgUrlList = this.$route.query.imgUrlList; //
				this.productDetail = this.$route.query.productDetail; //
			},
			shopcar() {
				let obj = this.obj;
				this.obj.count=this.count;
				let arr=this.arr;
				let lastInnerSize=this.obj.innerSize
				var temp=1;
				if(arr.length == 0) {
					console.log(0,arr)
					 arr.push(obj)
				} else {
					for(let i in arr) {
						if(arr[i].innerSize==obj.innerSize&&arr[i].pname==obj.pname){
							arr[i].count+=this.count
							temp--							
						}else{
							temp++							
						}
					}					
				}	
				if(temp>arr.length){
					this.arr.push(obj);
				}
				this.obj={
					innerSize: lastInnerSize, //
					count: 1, //
					pname: this.$route.query.pname,
					price: this.$route.query.price,
					firstClassAttrName: this.$route.query.firstClassAttrName, //
					proPictDir: this.$route.query.proPictDir,
					checked:true
				}																	
				this.$store.state.shopCarList = this.arr  //
				console.log(this.$store.state.shopCarList)
			},
			reduce(count) {
				this.count == 1 ? this.count = 1 : this.count--;
			},
			add(count) {
				this.count++
			},
			black(index) { //
				this.isBlack = index
				let getMenuText = this.$refs.menuItem[index].innerText;
				return this.obj.innerSize = getMenuText
			},
			show(index) { //
				this.isShow = !this.isShow //
				this.isActive = !this.isActive //
			},
			showImg(index) { // 
				this.indexImg = index
				this.isBorder = index
			},
			mouseEnter() {
				if(this.count == 1) {
					this.isDisable = true
				} else {
					this.isEnter = true
					this.isDisable = false
				}
			},
			mouseLeave() {
				this.isEnter = false
			},
			enterFocus() {
				this.isEnter = true
			},
			enterBlur() {
				this.isEnter = false
			}
		}
	}
</script>

<style lang="less">
	/**/
	
	.swiper-slide {
		height: 110px !important;
		width: 105px !important;
		float: left;
		img {
			height: 110px;
			width: 105px;
		}
	}
	
	.black {
		border: solid 1px black !importamt;
	}
	
	.show {
		height: 900px;
		width: 100%;
		margin-bottom: 50px;
	}
	
	.detailImg {
		text-align: center;
		padding-top: 80px;
	}
	/*<!---->*/
	
	.box {
		width: 100%;
		.product_box {
			width: 1200px;
			margin: 0 auto;
			.nav_header {
				display: flex;
				align-items: center;
				font-size: 14px;
				margin-top: 15px;
				margin-bottom: 15px;
				li {
					list-style: none;
					color: #333333;
					margin-right: 10px;
				}
				.right_icon {
					color: #CECBD7;
				}
			}
		}
	}
	/*  -*/
	
	.product_describe {
		width: 100%;
		height: 800px;
		>div {
			float: left;
			box-sizing: border-box;
			margin-right: 30px;
		}
		.product_img {
			width: 580px;
			height: 700px;
		}
		.product_info {
			width: 550px;
			height: 610px;
		}
	}
	/* */
	
	.product_img {
		.big_img {
			width: 580px;
			height: 580px;
			box-sizing: border-box;
			text-align: center;
			overflow: hidden;
			img {
				height: 580px;
				width: 580px;
			}
		}
		.small_img {
			margin-top: 10px;
			width: 580px;
			height: 110px;
			box-sizing: border-box;
			position: relative;
			>span {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
			}
			.after {
				position: absolute;
				right: 0;
			}
			ul {
				overflow: hidden;
				padding: 0 20px;
				box-sizing: border-box;
				li {
					list-style: none;
					float: left;
					height: 90px;
					width: 90px;
					text-align: center;
					margin-right: 5px;
					box-sizing: border-box;
					overflow: hidden;
					img {
						height: 85px;
						width: 85px;
						cursor: pointer;
						vertical-align: middle;
					}
				}
			}
		}
	}
	/**/
	/**/
	
	.parent {
		color: #111111 !important;
		border: solid 1px #111111 !important;
		position: relative;
		&:after {
			content: '';
			position: absolute;
			right: -6px;
			bottom: -6px;
			width: 12px;
			height: 12px;
			background: #111111;
			transform: rotate(-45deg);
		}
	}
	/**/
	
	.product_info {
		.product_name {
			color: #111;
			white-space: nowrap;
			text-overflow: ellipsis;
			word-break: keep-all;
			font-size: 16px;
		}
		.brand {
			padding-top: 20px;
			padding-bottom: 10px;
			border-bottom: 1px solid #d3d3d3;
			cursor: pointer;
			overflow: hidden;
			font-size: 14px;
			>li {
				list-style: none;
				float: left;
				color: #646464;
			}
			.code {
				float: right;
			}
		}
		.price {
			padding-bottom: 20px;
			padding-top: 20px;
			color: #111;
			font-size: 22px;
			.symbol {
				font-size: 14px;
			}
			.discount {
				color: #666;
				font-size: 14px;
				margin-left: 20px;
				text-decoration: line-through;
			}
		}
		.product_detail {
			width: 100%;
			>li {
				list-style: none;
				width: 100%;
				height: 50px;
				line-height: 50px;
				display: flex;
				font-size: 14px;
				.title {
					font-weight: 700;
				}
				.region {
					color: #c39a58;
					display: inline-block;
					margin-left: 40px;
					span:nth-child(2) {
						margin-left: 30px;
					}
				}
				.shop {
					color: #333;
					margin-left: 45px;
				}
				.choose {
					height: 30px;
					width: 50px;
					line-height: 30px;
					display: block;
					border: 1px solid #CDCDCD;
					text-align: center;
					cursor: pointer;
					margin-top: 10px;
					margin-right: 16px;
					float: left;
					position: relative;
					overflow: hidden;
				}
				.choose:first-child {
					margin-left: 55px;
				}
			}
			.input {
				margin-left: 28px;
				height: 40px;
				line-height: 40px;
				text-align: center;
				width: 128px;
				border: solid 1px #DCDFE6;
				position: relative;
				box-sizing: border-box;
				overflow: hidden;
				input {
					position: absolute;
					top: -1px;
					left: 0;
					height: 40px;
					width: 128px;
					border: none;
					border: dashed 1px blue;
					padding-left: 55px;
					padding-right: 50px;
					box-sizing: border-box;
					outline: none;
					overflow: hidden;
				}
				.reduce {
					position: absolute;
					left: 0;
					top: 0;
					width: 32px;
					height: 40px;
					line-height: 40px;
					text-align: center;
					color: #c0c4cc;
					box-sizing: border-box;
					font-size: 18px;
					background-color: #f7f7f7;
					border-right: solid 1px #dcdfe6;
					cursor: pointer;
				}
				.add {
					position: absolute;
					right: 0;
					top: 0;
					color: #959899;
					width: 32px;
					height: 40px;
					line-height: 40px;
					font-size: 18px;
					background-color: #f7f7f7;
					border-left: solid 1px #dcdfe6;
					cursor: pointer;
				}
			}
		}
	}
	/**/
	
	.service {
		overflow: hidden;
		margin-top: 15px;
		li {
			float: left;
			list-style: none;
			margin-right: 26px;
			font-size: 14px;
			i {
				color: #c39a58;
				font-size: 20px;
				margin-right: 5px;
				vertical-align: middle;
			}
		}
	}
	/* */
	
	.shopcar {
		margin-top: 15px;
		button {
			height: 50px;
			width: 200px;
			border: none;
			margin-right: 15px;
			line-height: 50px;
			text-align: center;
			color: #fff;
			font-size: 14px;
		}
		button:first-child {
			background-color: #000;
		}
		button:last-child {
			background-color: #c39a58;
		}
	}
	/**/
	
	.phone {
		margin-top: 20px;
		color: #333333;
		font-size: 14px;
		span {
			color: #c39a58;
			padding-right: 15px;
		}
	}
	/**/
	
	.shou {
		overflow: hidden;
		margin-top: 25px;
		font-size: 14px;
		li {
			float: left;
			list-style: none;
			margin-right: 25px;
			img {
				vertical-align: middle;
				margin-right: 8px;
			}
		}
	}
	/**/
	
	.nav_detail {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-bottom: 50px;
		li {
			list-style: none;
			margin-right: 140px;
			cursor: pointer;
			font-size: 15px;
			position: relative;
			padding-bottom: 15px;
			color: #666;
			a {
				color: #666;
				text-decoration: none;
			}
		}
		li.tab {
			font-size: 18px;
			color: #111;
			font-weight: 700;
			&:before {
				position: absolute;
				content: '';
				left: 10px;
				right: 10px;
				bottom: 0;
				height: 2px;
				background-color: #111;
				z-index: 100;
			}
		}
	}
	/* */
	
	.imgDetail {
		width: 100%;
		margin-top: 30px;
		text-align: center;
	}
	/**/
	
	.judgedetail {
		height: 300px;
		width: 100%;
		text-align: center;
	}
	/*input*/
	
	.black {
		border: solid 1px #111 !important;
	}
	/* */
	
	.border {
		border: solid 1px #111111;
	}
	/*1*/
	/* */
	
	.store {
		width: 22px;
		vertical-align: middle;
		margin-right: 5px;
	}
	/**/
	
	.title_product {
		padding-bottom: 45px;
		font-size: 16px;
		font-weight: 700;
	}
	
	.one {
		display: flex;
		align-items: center;
		margin-bottom: 15px;
		>li {
			width: 275px;
			font-size: 14px;
			color: #333;
			list-style: none;
		}
	}
	
	.disable {
		cursor: not-allowed !important;
	}
</style>


